'use client';

import { useEffect, useState } from 'react';
import api from '@/services/api';

import {
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  Typography,
  CircularProgress,
  Box,
} from '@mui/material';

interface Participante {
  id: number;
  nombreEquipo: string;
  nombreCompleto: string;
  departamento: string;
  provincia: string;
  municipio: string;
  documentoIdentidad: string;
  rol: string;
}

export default function ParticipantesPage() {
  const [participantes, setParticipantes] = useState<Participante[]>([]);
  const [loading, setLoading] = useState<boolean>(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    setLoading(true);
    api.get('/participantes')
      .then((res) => {
        setParticipantes(res.data);
        setError(null);
      })
      .catch((err) => {
        console.error('Error al obtener participantes:', err);
        setError('Error cargando los participantes.');
      })
      .finally(() => setLoading(false));
  }, []);

  if (loading) {
    return (
      <Box display="flex" justifyContent="center" mt={4}>
        <CircularProgress />
      </Box>
    );
  }

  if (error) {
    return (
      <Typography color="error" mt={4} align="center">
        {error}
      </Typography>
    );
  }

  return (
    <Box mt={4} mx={2}>
      <Typography variant="h4" gutterBottom>
        Participantes Registrados
      </Typography>

      {participantes.length === 0 ? (
        <Typography>No hay participantes registrados.</Typography>
      ) : (
        <TableContainer component={Paper}>
          <Table aria-label="tabla participantes">
            <TableHead>
              <TableRow>
                <TableCell>Equipo</TableCell>
                <TableCell>Nombre Completo</TableCell>
                <TableCell>Departamento</TableCell>
                <TableCell>Provincia</TableCell>
                <TableCell>Municipio</TableCell>
                <TableCell>DNI</TableCell>
                <TableCell>Rol</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {participantes.map((p) => (
                <TableRow key={p.id}>
                  <TableCell>{p.nombreEquipo}</TableCell>
                  <TableCell>{p.nombreCompleto}</TableCell>
                  <TableCell>{p.departamento}</TableCell>
                  <TableCell>{p.provincia}</TableCell>
                  <TableCell>{p.municipio}</TableCell>
                  <TableCell>{p.documentoIdentidad}</TableCell>
                  <TableCell>{p.rol}</TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      )}
    </Box>
  );
}
